

.progressbar {
    display: flex;
    width: 77vw;
    height: 4rem;
    border: 3px solid #f3f3f3;
    margin: 0px auto;
    position: relative;
    border-radius: 6px;

    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: bold;
    }

    &__value {

    }

    & .plan-marker {
        
        display: block;
        position: absolute;
        width: 6px;
        height: 60px;
        top: 0;
        background-color: rgb(245, 69, 16);


        &::after {
            content: "Plan";
            position: absolute;
            top: -30px;
            left: -16px;
            color: red;
            text-transform: uppercase;
            font-weight: bold;
        }

    }
    @media (max-width: 1350px) { 
        width: 70vw;

     }
}

.x-axis {
    display: flex;
    margin: 0px auto;
    width: 77vw;
    position: relative;
    @media (max-width: 1350px) { 
        width: 70vw;

     }

}

.point {
    display: block;

    &__number {
        position: absolute;
        transform: translateX(-50%);
        //right: -24px;

        &::before {
            content: "";
            display: inline-block;
            position: absolute;
            width: 1px;
            height: 20px;
            right: 50%;
            background-color: black;
            top: -1.1rem;
        }
    }

}

.percentage {
    display: flex;
    justify-content: space-evenly;
    width: 40vw;
    position: absolute;
    top: -2.5rem;
    left: 41vw;
    font-size: 1.2rem;
    
}
.name {
    position: absolute;
    top: 1.2rem;
    left: 2rem;
    position: absolute;
    top: 1.5rem;
    left:  5rem;
    font-weight: bold;
    text-transform: uppercase;
    @media (max-width: 1630px) { 
        left:  3rem;
     }
     @media (max-width: 1050px) { 
        left:  1.5rem;
        font-size: 10px;
     }
     @media (max-width: 730px) { 
        left:  0.5rem;
        font-size: 8px;
     }
}

.spinner {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.fire-icon {

    width: 20px;
    height: 20px;
    margin-right: 5px;
}

